import { Promise } from "q";

var util = {}
var YYAppReady = false
util.init = function () {
  document.addEventListener('hsAppReady', function () {
    YYAppReady = true
  }, false)
  console.log('init')
  console.log(YYAppReady)
}
util.callNative = function (param) {
  if (window.winner) {
    var params = JSON.stringify(param)
    winner.hsJsFunction(params)
  } else {
    console.log('--------------++' + YYAppReady)
    if (YYAppReady) {
      hsJsFunction(param)
      console.log("--------------++");
    } else {
      document.addEventListener("hsAppReady", function () {
        YYAppReady = true
        hsJsFunction(param)
      }, false)
    }
  }
}
util.finish = function () {
  var param = {
    provider: {
      providerName: "xsdzq"
    },
    funType: "forward",
    funName: "finish"
  };
  this.callNative(param);
}
util.share = function (url, title, description) {
  var param = {
    provider: {
      providerName: 'xsdzq'
    },
    funType: 'share',
    funName: 'WXshare',
    url: url,
    title: title,
    description: description,
    right: 'share'
  }
  this.callNative(param)
}

util.shareCallBack = function (url, title, description, callBack) {
  var param = {
    provider: {
      providerName: 'xsdzq'
    },
    funType: 'share',
    funName: 'WXshare',
    outputParam: {
      client_id: '',
      fundaccount: ''
    },
    url: url,
    title: title,
    description: description,
    right: 'share',
    callBack: callBack
  }
  this.callNative(param)
}

