import axios from '@/libs/api.request'

export const getProductList = (data) => {
  return axios.request({
    url: '/activity/product/list',
    method: 'get',
    params: data
  })
}

export const getAllProduct = () => {
  return axios.request({
    url: '/activity/product/all',
    method: 'get'
  })
}
