<script>
import { Header, Button, Toast, MessageBox, Indicator } from 'mint-ui';
import { browser } from './libs/utils'
import { getWxSign } from './api/wx'
import jsAPI from './libs/jsAPI'

export default {
  name: 'Main',
  data() {
    return {
      ios: false,
      popupVisible: false,
      isBack: true,
      xsdzqOpen: false,
      titleObject: {
        back: false,
        share: false,
        romoveMarginTop: false,
        title: "",
        shareTitle: "",
        description: ""
      }
    }
  },
  created() {
    this.initAPP();
    this.initShare();
  },
  components: {

  },
  methods: {
    initAPP: function () {
      jsAPI.init();
    },
    initShare: function () {
      let url = window.location.href;
      let appId = 'wx0634e6e147527eff';
      let title = '';
      let imageUrl = 'https://activity.e95399.com/images/logo.png';
      let desc = 'call';
      let params = {
        url: url,
        appid: 'gh_e9f8177a716b'
      }
      getWxSign(params).then(res => {
        if (res.data.resCode == 0) {
          //
          console.log(res);
          let resData = res.data.result;
          let signature = resData.signature;
          let noncestr = resData.noncestr;
          let timestamp = resData.timestamp;
          console.log(signature);
          console.log(noncestr);
          console.log(timestamp);
          if (signature && noncestr && timestamp && wx) {
            console.log(signature);
            console.log(noncestr);
            console.log(timestamp);
            wx.config({
              debug: false,
              appId: appId,
              timestamp: timestamp,
              nonceStr: noncestr,
              signature: signature,
              jsApiList: [
                'checkJsApi',
                'onMenuShareTimeline',
                'onMenuShareAppMessage'
              ]
            });
            wx.ready(function () {
              //alert('success');
              let pageShareData = {
                title: title,
                desc: desc,
                // it's ok to have GA here
                // if there is query string(e.g. start with '?' character
                // then new queries should append with '&' character
                // double '?' characters is invalid syntax
                link: url,
                imgUrl: imageUrl,
                success: function (res) {

                },
                cancel: function (res) {
                  console.log('Cancelled page sharing for:', location.href);
                }
              };
              wx.onMenuShareTimeline(pageShareData);
              wx.onMenuShareAppMessage(pageShareData);
            });
            wx.error(function (res) {
              //alert(JSON.stringify(res));
              console.log('wechat jssdk error: ', res);
            });
          }
        } else {
          return;
          Toast({
            message: res.data.resMsg,
            position: 'middle',
            duration: 3000
          });
        }
      });

    },
    goBack: function () {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/');
    },
    routerChange: function (data, callBack) {
      console.log(data);
      jsAPI.setNav(data, callBack);
    },
    handleClose: function () {

    },
    toShare: function () {

    }
  }
}
</script>
