import axios from '@/libs/api.request'

export const getAwards = () => {
  return axios.request({
    url: '/activity/award/all',
    method: 'get'
  })
}

export const convertAwards = (data) => {
  return axios.request({
    url: '/activity/award/convert',
    method: 'post',
    data
  })
}

export const myAwards = () => {
  return axios.request({
    url: '/activity/award/record',
    method: 'get'
  })
}