<template>
  <div id="activity">
    <div class="top-wrapper">
      <div class="activity-action" @click="gotoRule">
        <p></p>
      </div>
      <div class="turnpoint-wrapper">
        <div class="eleven-img-wrapper">
          <img class="gift-content img-responsive" @click="gotoGift" src="../assets/my-award.png" />
          <img class="eleven-img img-responsive" src="../assets/eleven.png" />
        </div>
        <div class="turn-bg-wrapper">
          <img id="turnOuter" class="turn-outer img-responsive" src="../assets/turn-outer.png" />
          <img class="turn-inner img-responsive" src="../assets/turn-inner.png" />
          <TurnPoint :prizeNumber="prizeNumber" @login='forwardLogin' :clientId="userInfo.clientId" @result="awardResult" />
        </div>
        <div class="marquee-wrapper" v-if="marqueeData.length>0">
          <Marquee :lists="marqueeData"></Marquee>
        </div>
        <div class="award-tip">
          <span class="change-wrapper">
            
            <span class="number" v-if="prizeNumber>=0">{{prizeNumber}} </span>
            <span class="please-login" @click="forwardLogin" v-else></span>
          </span>
          <span class="change-detail" @click="gotoTicketDetail"></span>
        </div>
      </div>
      <img class="top-img img-responsive" src="../assets/top-bg.png" />
    </div>
    <div class="action-wrapper">
      <div class="go-wrapper">
        <img @click="forwardPageByAction('forwarTougu')" class="go-img" src="../assets/go1.png" />
        <img @click="forwardLicai" class="go-img" src="../assets/go2.png" />
        <img @click="forwardPageByAction('forwardCrhBusiness')" class="go-img" src="../assets/go3.png" />
        <img @click="gotoQq" class="go-img" src="../assets/go4.png" />
      </div>
      <img class="img-responsive" src="../assets/activity-bg.png" />
    </div>
    <div class="sx-wrapper">
      <div class="zodiac-wrapper">
        <div class="top" v-if="zodiacList.length > 2">
          <div class="left">
            <span>{{zodiacList[0].num}}</span>
          </div>
          <div class="right">
            <span>{{zodiacList[1].num}}</span>
          </div>
        </div>
        <div class="zodiac-list-wrapper">
          <div v-for="(item,index) in lowerZodiacList" :key="index">
            <div class="zodiac-item-wrapper">
              <span>{{item.prizeEntity.name | getOne}}</span>
              <span>{{item.num}}</span>
            </div>
          </div>
        </div>
        <div class="zodiac-total-number">
          <p><span></span><span>{{getZodiacNumber}}</span></p>
        </div>
      </div>
      <img class="img-responsive" src="../assets/activity-index.png" />
    </div>
    <div class="award-list-wrapper">
      <div class="award-top-wrapper">
        <img class="img-responsive" src="../assets/award-top.png" />
      </div>
      <div class="award-item-wrapper">
        <div class="award-item-outer" v-for="(item, index) in awardList" :key="index">
          <div class="award-item">
            <img class="img-responsive text-img" :src="getAwardTextImageSrc(item.imageName)" />
            <img class="img-responsive" :src="getAwardCardImageSrc(item.imageName)" />
            <div class="action-wrapper">
              <img @click="reduceAwardNumber(index)" class="action-img" src="../assets/award-reduce.png" />
              <input type="number" @change="changeAwardNumber(index)" class="action-number" v-model="availableAwardNumber[index].availableAward" />
              <!-- <p class="action-number">{{availableAwardNumber[index].availableAward}}</p>-->
              <img @click="addAwardNumber(index)" class="action-img" src="../assets/award-add.png" />
            </div>
            <span class="text"><span>{{getAwardNumber(index)}}</span></span>
            <span class="convert" @click="convertAward(index)"></span>
          </div>
        </div>
        <div class="clear"></div>
        <div class="huawei-wrapper" v-if="awardList.length > 3">
          <p>Mate 30  <span>{{awardList[3].imageNumber}}</span> </p>
        </div>
      </div>
    </div>
    <div class="open" v-if="xsdzqOpen">
      <div class="content" @click="openApp">
        <div>APP</div>
      </div>
    </div>
    <PopupContainer :isPopup="awardPopupVisible">
      <div class="award-popup-wrapper">
        <img class="award-popup-image img-responsive" src="../assets/award-result-bg.png" />
        <div class="award-popup-content">
          <p class="award-popup-message">{{awardResutlString}}</p>
          <span @click="colseAwardResultPopup" class="award-popup-btn"></span>
        </div>
      </div>
    </PopupContainer>
    <mt-popup v-model="openPopupVisible" popup-transition="popup-fade" position="top">
      <div class="open-wrap">
        <img class="img-responsive" src="../assets/open.png">
      </div>
    </mt-popup>
    <div class="tip-money-wrapper">
      <p> 5000</p>
    </div>
    <div class="footer">
      <p @click="sharePrize">
        <span> </span>
      </p>
    </div>
  </div>
</template>
<script>
import TurnPoint from '@/components/TurnPoint.vue'
import Marquee from '@/components/Marquee.vue'
import PopupContainer from '@/components/PopupContainer'
import jsAPI from '../libs/jsAPI'
import { truncate } from 'fs';
import { getPrizes, getLatestPrize, getPrizeNumber, sharePutPrizeNumber, getMyPrizeRecord } from '@/api/prize'
import { getAwards, convertAwards } from '@/api/award'
import { getTicketNumber } from '@/api/ticket'
import { Toast, MessageBox, Indicator } from 'mint-ui';
import { browser } from '@/libs/utils'

export default {
  name: 'activity',
  components: {
    TurnPoint,
    Marquee,
    PopupContainer
  },
  data() {
    return {
      awards: [],
      isFans: true,
      isStar: false,
      isXsdzq: false,
      prizePopupVisible: true,
      awardPopupVisible: false,
      awardResutlString: '',
      xsdzqOpen: false,
      openPopupVisible: false,
      prizes: [],
      marqueeData: [],
      empList: [],
      awardList: [],
      productList: [],
      availableAwardNumber: [{
        availableAward: ''
      }, {
        availableAward: ''
      }, {
        availableAward: ''
      }, {
        availableAward: ''
      }
      ],
      awardTextArray: [
        {
          name: 'nqctj',
          srcName: require('../assets/nqctj-text.png')
        },
        {
          name: 'sysej',
          srcName: require('../assets/sysej-text.png')
        },
        {
          name: 'snznj',
          srcName: require('../assets/snznj-text.png')
        },
        {
          name: 'qjfdj',
          srcName: require('../assets/qjfdj-text.png')
        }
      ],
      awardCardArray: [
        {
          name: 'nqctj',
          srcName: require('../assets/nqctj.png')
        },
        {
          name: 'sysej',
          srcName: require('../assets/sysej.png')
        },
        {
          name: 'snznj',
          srcName: require('../assets/snznj.png')
        },
        {
          name: 'qjfdj',
          srcName: require('../assets/qjfdj.png')
        }
      ]
    }
  },
  computed: {
    userInfo() {
      return this.$store.state.user;
    },
    count() {
      return this.$store.state.count;
    },
    prizeNumber() {
      return this.$store.state.prizeNumber
    },
    ticketNumber() {
      return this.$store.state.ticketNumber
    },
    zodiacList() {
      return this.$store.state.zodiacList
    },
    lowerZodiacList() {
      let arr = [];
      for (let i = 2; i < this.$store.state.zodiacList.length; i++) {
        arr.push(this.$store.state.zodiacList[i]);
      }
      return arr;
    },
    getZodiacNumber() {
      let value = this.$store.state.zodiacList[0].num;
      if (parseFloat(value).toString() === 'NaN') {
        return '--'
      }
      let total = 0;
      for (let i = 0; i < this.$store.state.zodiacList.length; i++) {
        total += this.$store.state.zodiacList[i].num;
      }
      return total;
    }
  },
  created() {
    /*Indicator.open({
      text: 'Loading...',
      spinnerType: 'snake'
    });*/
    let that = this;
    window.acitityShareFun = function (data) {
      console.log('---------share---------');
      console.log(data);
      //that.shareActivity();
    }
    let titleObject = {
      back: true,
      share: true,
      romoveMarginTop: false,
      title: "SHU ",
      shareTitle: "",
      description: "call",
      rightBtns: [
        {
          btnTitle: '',
          iconUrl: 'https://activity.e95399.com/images/share.png',
          callBack: 'acitityShareFun'
        }
      ]
    }

    this.$emit("routerChange", titleObject);
    if (!browser().isXsdzq) {
      this.xsdzqOpen = false;
      this.isXsdzq = true;
    } else {
      this.xsdzqOpen = true;
      this.isXsdzq = false;
    }
    // 1.
    this.initUserInfo();
    // 2.
    this.getLatestPrize();
    // 3. 
    //this.getPrizes();
    // 4.
    this.getAwards();
    // 5.
    //this.getUserTicketSort();
  },
  filters: {
    getOne(str) {
      if (str.length > 1) {
        return str.substring(0, 1);
      }
      return str
    }
  },
  methods: {
    getMyPrizeRecord() {
      getMyPrizeRecord().then(res => {

      });
    },
    getAwardTextImageSrc(name) {
      let src = '';
      for (let index in this.awardTextArray) {
        if (this.awardTextArray[index].name === name) {
          src = this.awardTextArray[index].srcName;
        }
      }
      return src;
    },
    getAwardCardImageSrc(name) {
      let src = '';
      for (let index in this.awardCardArray) {
        if (this.awardCardArray[index].name === name) {
          src = this.awardCardArray[index].srcName;
        }
      }
      return src;
    },
    gotoQq() {
      MessageBox({
        message: '95399',
        confirmButtonText: ''
      }).then(action => {
        console.log(action);
        if (action == 'confirm') {
          //that.share();
        } else if (action == 'cancel') {
          // that.forwardLogin();
        }
      });
    },
    getLatestPrize: function () {
      //
      getLatestPrize().then(res => {
        let tipList = [];
        if (res.data.resCode === 0) {
          let data = res.data.result;
          if (data !== undefined) {
            tipList.push('1')
            tipList.push(data.userEntity.clientId);
            tipList.push('' + data.prizeEntity.name);
          }
          this.marqueeData = tipList;
        }
      });
    },
    initUserInfo: function () {
      if (this.isXsdzq) {
        this.$store.dispatch('userLogin');
      }
    },
    getAwards: function () {
      getAwards().then(res => {
        if (res.data.resCode === 0) {
          //
          this.awardList = res.data.result;
        } else {
          Toast({
            message: res.data.resMsg,
            position: 'middle',
            duration: 3000
          });
        }
      });
    },
    getAwardNumber: function (index) {
      if (this.zodiacList.length <= 0) {
        return 0;
      }
      let award = this.awardList[index];
      let num = 0;
      if (index === 0) {
        // 1
        for (let index in this.zodiacList) {
          if (this.zodiacList[index].prizeEntity.name === "") {
            num = this.zodiacList[index].num;
            return num;
          }
        }
      } else if (index === 1) {
        //
        for (let index in this.zodiacList) {
          if (this.zodiacList[index].prizeEntity.name === "") {
            let temp = this.zodiacList[index].num;
            if (temp >= 2) {
              num = temp / 2;
            }
            return num;
          }
        }
      } else if (index === 2) {
        //
        let shuCardNumber = 0;
        let niuCardNumber = 0;
        for (let index in this.zodiacList) {
          if (this.zodiacList[index].prizeEntity.name === "") {
            shuCardNumber = this.zodiacList[index].num;
          }
          if (this.zodiacList[index].prizeEntity.name === "") {
            shuCardNumber = this.zodiacList[index].num;
          }
        }
        if (shuCardNumber >= niuCardNumber) {
          return niuCardNumber;
        }
        return shuCardNumber;
      } else if (index === 3) {
        // 
        let min = this.zodiacList[0].num;
        for (let i = 1; i < this.zodiacList.length; i++) {
          if (min > this.zodiacList[i].num) {
            min = this.zodiacList[i].num;
          }
        }
        return min;
      }
      return num;
    },
    changeAwardNumber: function (index) {
      console.log('change: ' + index);
      let award = this.awardList[index];
      let max = this.getAwardNumber(index);
      let value = this.availableAwardNumber[index].availableAward;
      if (parseFloat(value).toString() === 'NaN' || value <= 0) {
        MessageBox({
          message: '',
          confirmButtonText: ''
        })
        this.availableAwardNumber[index].availableAward = '';
        return;
      }
      if (value > max) {
        let message = '' + max;
        message += award.awardName;
        message += ','
        MessageBox({
          message: message,
          confirmButtonText: ''
        })
        this.availableAwardNumber[index].availableAward = '';
      }
    },
    addAwardNumber: function (index) {
      let award = this.awardList[index];
      let max = this.getAwardNumber(index);
      if (max === 0) {
        MessageBox({
          message: '',
          confirmButtonText: ''
        })
        return;
      }
      let current = this.availableAwardNumber[index].availableAward;
      if (current === '') {
        current = 0;
        if (max > 0) {
          this.availableAwardNumber[index].availableAward = 1
        }
        return;
      }
      if (current > max) {
        let message = '' + max;
        message += award.awardName;
        message += ','
        MessageBox({
          message: message,
          confirmButtonText: ''
        })
        return
      }
      this.availableAwardNumber[index].availableAward += 1;
    },
    reduceAwardNumber: function (index) {
      let max = this.getAwardNumber(index);
      if (max === 0) {
        MessageBox({
          message: '',
          confirmButtonText: ''
        })
        return;
      }
      let current = this.availableAwardNumber[index].availableAward;
      if (current === '') {
        return;
      }
      if (current === 0) {
        return;
      }
      this.availableAwardNumber[index].availableAward -= 1;
    },
    convertAward: function (index) {
      // 
      let value = this.availableAwardNumber[index].availableAward;
      let max = this.getAwardNumber(index);
      let award = this.awardList[index];
      if (parseFloat(value).toString() === 'NaN' || value <= 0) {
        MessageBox({
          message: '',
          confirmButtonText: ''
        })
        this.availableAwardNumber[index].availableAward = '';
        return;
      }
      if (value > max) {
        let message = '' + max;
        message += award.awardName;
        message += ','
        MessageBox({
          message: message,
          confirmButtonText: ''
        })
        this.availableAwardNumber[index].availableAward = '';
        return;
      }
      let params = {
        award,
        num: value
      }
      let message = '';
      if (award.index === 1) {
        message = '' + '<font color="red">' + value + '</font>' + '' + award.awardName + '' + '<font color="red">' + value + '</font>' + '';
      } else if (award.index === 2) {
        message = '' + '<font color="red">' + value + '</font>' + '' + award.awardName + '' + '<font color="red">' + value * 2 + '</font>' + '';
      } else if (award.index === 3) {
        message = '' + '<font color="red">' + value + '</font>' + '' + award.awardName + '' + '<font color="red">' + value + '</font>' + '' + '< font color="red" > ' + value + '</font>' + ''
      } else if (award.index === 4) {
        message = '' + '<font color="red">' + value + '</font>' + '' + award.awardName + '' + '<font color="red">' + value + '</font>' + '';
      }
      MessageBox({
        message: message,
        confirmButtonText: '',
        cancelButtonText: '',
        showCancelButton: true
      }).then(action => {
        console.log(action);
        if (action === 'confirm') {
          this.convertRealAward(params);
        } else if (action === 'cancel') {
        }
      });
    },
    convertRealAward: function (data) {
      Indicator.open({
        text: 'Loading...',
        spinnerType: 'snake'
      });
      let params = data;
      convertAwards(params).then(res => {
        if (res.data.resCode === 0) {
          //
          this.$store.dispatch('getZodiacList');
          Indicator.close();
          let message = '' + data.num + '' + data.award.awardName;
          if (data.award.index === 1) {
            message += ',' + data.num + '50E';
          } else if (data.award.index === 2) {
            message += ',' + data.num + '100E';
          } else if (data.award.index === 3) {
            message += ',' + data.num + '500E';
          } else if (data.award.index === 4) {
            message += ',' + data.num + '3999Mate30';
          }
          this.awardResutlString = message;
          this.awardPopupVisible = true;
        } else {
          Toast({
            message: res.data.resMsg,
            position: 'middle',
            duration: 3000
          });
        }
      });
    },
    colseAwardResultPopup: function () {
      this.awardPopupVisible = false;
      this.awardResutlString = '';
    },
    getPrizeNumber: function () {
      getPrizeNumber().then(res => {
        //this.prizeNumber = res.data.result.number;
        this.$store.commit({
          type: 'setPrizeNumber',
          prizeNumber: res.data.result.number
        });
      });
    },
    getTicketNumber: function () {
      getTicketNumber().then(res => {
        //this.ticketNumber = res.data.result.number;
        this.$store.commit({
          type: 'setTicketNumber',
          ticketNumber: res.data.result.number
        });
      });
    },
    handleShare: function () {
      jsAPI.share('https://www.baidu.com', 'title', 'description');
    },
    sharePrize: function () {
      sharePutPrizeNumber().then(res => {
        if (res.data.resCode == 0) {
          //
          this.getPrizeNumber();
          //this.getTicketNumber();
          Toast({
            message: '',
            position: 'middle',
            duration: 3000
          });
        }
      });
    },
    shareActivity: function () {
      let that = this;
      if (!that.hasLogin()) {
        MessageBox({
          message: '',
          confirmButtonText: '',
          cancelButtonText: '',
          showCancelButton: true
        }).then(action => {
          console.log(action);
          if (action === 'confirm') {
            that.share();
          } else if (action === 'cancel') {
            that.forwardLogin();
          }
        });
      } else {
        that.share();
      }
    },
    share: function () {
      let that = this;
      let url = window.location.href;
      let title = '';
      let description = 'call';
      console.log("description:" + description);
      window.shareCallBackLiCai = function (data) {
        console.log('shareCallBackLiCai');
        console.log(data);
        that.sharePrize();
      }
      jsAPI.shareCallBack(url, title, description, 'shareCallBackLiCai');
    },
    forwardLogin: function () {
      if (this.isXsdzq) {
        let that = this;
        window.loginCallBack = function () {
          // 
          console.log('loginCallBack');
          that.initUserInfo();
        }
        jsAPI.forwardLogin('loginCallBack');
      } else {
        this.openApp();
      }
    },
    forwardPageByAction: function (action) {
      jsAPI.forwardPageByAction(action);
    },
    forwardPageById: function (pageId) {
      console.log(pageId);
      jsAPI.forwardPage(pageId);
    },
    handleNav: function () {
      jsAPI.setNav();
    },
    handleLogin: function () {
      let that = this;
      window.callUserBack = function (data) {
        console.log('-------------');
        console.log(data);
        alert(JSON.stringify(data));
        console.log(that.copyData);
        if (that.hasLogin()) {

        }
      }
      jsAPI.getUserInfo('callUserBack');
    },
    getPrizes: function () {
      let that = this;
      getPrizes().then(function (res) {
        if (res.data.resCode == 0) {
          //
          that.prizes = res.data.result;
        }
      });
    },
    awardResult: function () {
      this.getPrizeNumber();
      this.$store.dispatch('getZodiacList');
      //this.getTicketNumber();
    },
    changeSort: function (flag) {
      if (flag == 'fans') {
        this.getUserTicketSort();
        this.isFans = true;
        this.isStar = false;
      } else {
        //this.getEmpList();
        this.isStar = true;
        this.isFans = false;
      }
    },
    hasLogin: function () {
      if (this.userInfo.clientId.length > 1) {
        return true;
      }
      return false;
    },
    gotoGift: function () {
      if (this.userInfo.clientId.length < 1) {
        this.forwardLogin();
        return;
      }
      this.$router.push('/gift')
    },
    forwardLicai: function () {
      if (this.userInfo.clientId.length < 1) {
        this.forwardLogin();
        return;
      }
      this.$router.push('/hot')
    },
    gotoTicketDetail: function () {
      console.log('gotoTicketDetail');
      if (this.hasLogin()) {
        this.$router.push('/ticketDetail')
      } else {
        this.forwardLogin();
      }
    },
    getMoreActivity: function () {
      if (this.hasLogin()) {
        this.$router.push('/myActivity');
      } else {
        this.forwardLogin();
      }
    },
    gotoRule: function () {
      if (this.hasLogin()) {
        this.$router.push('/rule')
      } else {
        this.forwardLogin();
      }
    },
    gotoComplete: function () {
      if (this.isFans) {
        this.$router.push('/fans')
      } else {
        this.$router.push('/emp');
      }
    },
    gotoHot: function () {
      if (this.hasLogin()) {
        this.$router.push('/hot')
      } else {
        this.forwardLogin();
      }
    },
    openApp: function () {
      if (browser().isWX) {
        this.openPopupVisible = true;
        return;
      } else {
        this.openPopupVisible = false;
      }
      let url = window.location.href;
      let search = window.location.search;
      console.log("url: " + url);
      console.log("search: " + search);
      //
      let openUrl = location.href; // + "//" + location.hostname + ":" + location.port + "/front/" + search + "/" + location.hash;
      console.log(openUrl);
      let paramsObject = {
        openUrl: openUrl,
        openPageType: "openNewWebPage"
      };
      let params = encodeURIComponent(JSON.stringify(paramsObject));
      if (browser().isIOS) {
        this.openIos(params);
      } else if (browser().isAndroid) {
        this.openAndroid(params);
      }
    },
    openAndroid: function (params) {
      console.log(params);
      let newUrl = "xsdzq1://" + "com.hundsun.stockwinner.xsdzq1" + "?" + params;
      window.location.href = newUrl;
      setTimeout(function () {
        window.location = "http://cp.xsdzq.cn/wx/index.html";
      }, 2600);
    },
    openIos: function (params) {
      let newUrl = "https://apple.e95399.com?" + "params=" + params;
      window.location.href = newUrl;
    }
  }
}
