import CryptoJS from 'crypto-js'
export const TOKEN_KEY = 'token'

export const setToken = (token) => {
  localStorage.setItem('token', token)
}

export const getToken = () => {
  const token = localStorage.getItem('token')
  if (token) return token
  else return false
}

export const clearToken = () => {
  localStorage.removeItem('token')
}

const getKey = function () { return '\x78\x73\x64\x7a\x71\x6c\x69\x63\x61\x69\x6a\x69\x65\x38\x38\x38' }
const getIv = function () { return '\x69\x71\x39\x6e\x41\x70\x77\x51\x66\x67\x39\x4f\x46\x58\x56\x70' }

export const aesEncrypt = (content) => {
  let keyString = getKey()
  let ivString = getIv()
  let key = CryptoJS.enc.Latin1.parse(keyString)
  let iv = CryptoJS.enc.Latin1.parse(ivString)
  let encrypted = CryptoJS.AES.encrypt(content, key, {
    iv: iv,
    mode: CryptoJS.mode.CBC,
    padding: CryptoJS.pad.ZeroPadding
  })
  return encrypted.toString()
}

export const browser = () => {
  var userAgent = navigator.userAgent
  console.log(userAgent)
  var webview = {}
  webview.isXsdzq = (function () {
    return userAgent.indexOf('xsdzq') > 0 || userAgent.indexOf('XSDZQ') > 0
  })()
  webview.isChrome = (function () {
    return userAgent.indexOf('Chrome') > 0 || userAgent.indexOf('CriOS') > 0
  })()
  webview.isIOS = (function () {
    return /(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)
  })()
  webview.isAndroid = (function () {
    return userAgent.indexOf('Android') > -1 || userAgent.indexOf('Adr') > -1 //android
  })()
  var lower = userAgent.toLowerCase()
  webview.isWX = (function () {
    return /micromessenger/.test(lower)
  })()
  return webview
}
