import axios from '@/libs/api.request'

export const getTicketNumber = () => {
  return axios.request({
    url: '/activity/ticket/number',
    method: 'get'
  })
}

export const userVoteEmp = (data) => {
  return axios.request({
    url: '/activity/ticket/vote',
    method: 'post',
    data
  })
}

export const getUserTicketSort = (data) => {
  return axios.request({
    url: '/activity/ticket/userSort',
    method: 'get',
    params: data
  })
}

export const getUserTicketRecord = (data) => {
  return axios.request({
    url: '/activity/ticket/userRecord',
    method: 'get',
    params: data
  })
}

export const getMyVoteEmp = (data) => {
  return axios.request({
    url: '/activity/ticket/myVoteEmp',
    method: 'get',
    params: data
  })
}
