import { getMyPrize } from '@/api/prize'
import { getProductList, getAllProduct } from '@/api/product'
export default {
  name: 'TurnPoint',
  components: {
    ProductItem,
    PopupContainer
  },
  props: {
    prizeNumber: Number,
    clientId: String
  },
  data() {
    return {
      prize: {},
      isClick: true,
      timer: null,
      timer2: null,
      timerOut: null,
      timerOuter: null,
      text: '', // ,
      srcName: require('../assets/point.png'),
      productList: [],
      intervalTime: 1000,
      imageFlag: false,
      popupVisible: false,
      closeOnClickModal: false,
      prizePopupVisible: false,
      sxArrary: [
        {
          name: 'shu',
          srcName: require('../assets/sx/shu.png')
        },
        {
          name: 'niu',
          srcName: require('../assets/sx/niu.png')
        },
        {
          name: 'hu',
          srcName: require('../assets/sx/hu.png')
        },
        {
          name: 'tu',
