  },
  created() {
    this.getProductList();
  },
  methods: {
    lottery() {
      console.log('------');
      if (!this.isClick) {
        console.log('------');
        return;
      }
      console.log('');
      // 
      // 
      let that = this;
      console.log(this.prizeNumber);
      if (this.prizeNumber >= 0) {
        // 
        if (this.prizeNumber == 0) {
          //0
          MessageBox({
            message: '',
            confirmButtonText: ''
          }).then(action => {
            console.log(action);
            if (action === 'confirm') {

            } else if (action === 'cancel') {
            }
          });
          return;
        }
      } else {
        //
        this.$emit('login');
        return;
      }
      this.isClick = false;
      this.animation();
      //
      getMyPrize().then(function (res) {
        console.log(res);
        if (res.data.resCode == 0) {
          //
          that.setStopData(res.data.result);
          let name = res.data.result.image;
          for (let i = 0; i < that.sxArrary.length; i++) {
            let obj = that.sxArrary[i];
            if (name === obj.name) {
              that.prizeSx = obj.srcName;
            }
          }
          that.$emit('result');
        } else {
          that.stopAnimationNoLogin();
          if (res.data.resCode == 1) {
            that.$store.dispatch('userLogin');
          }
        }
      });
      //this.getProductList();
    },
    animation() {
      this.imageFlag = true;
      this.gotoTurn();
    },
    gotoTurn: function () {
      let outerImage = document.getElementById('turnOuter');
      let flag = true;
      let count = 0;
      this.timerOuter = setInterval(() => {
        if (flag) {
          outerImage.style.transform = "rotate(15deg)";
        } else {
          outerImage.style.transform = "rotate(30deg)";
        }
        flag = !flag;
      }, 200);
    },
    stopTurn: function () {
      //let outerImage = document.getElementById('turnOuter');
      clearInterval(this.timerOuter);
    },
    setStopData: function (prize) {
      console.log(prize);
      let that = this;
      this.timerOut = setTimeout(() => {
        that.prize = prize;
        this.imageFlag = false;
        that.rotateImage();
      }, 1500);
    },
    rotateImage() {
      let image = document.getElementById('point-image');
      let deg = 0;
      let count = 0;
      this.timer = setInterval(() => {
        count += 1;
        let type = count % 12;
        deg = type * 30
        image.style.transform = "rotate(" + deg + "deg)";
      }, 200);

      let that = this;
      this.timer2 = setTimeout(() => {
        clearInterval(that.timer);
        that.stopTurn();
        that.stopAnimation();
        that.isClick = true;
      }, 1500);

      /*function turn() {
        count += 1;
        let type = count % 12;
        deg = type * 30 - 15
        image.style.transform = "rotate(" + deg + "deg)";
        if (count < 100) {
          window.requestAnimationFrame(turn);
        }
      }
      window.requestAnimationFrame(turn);*/
    },
    startAnimation() {

    },
    stopAnimation() {
      let image = document.getElementById('point-image');
      let type = this.prize.id;
      let degree = (type - 1) * 30;
      image.style.transform = "rotate(" + degree + "deg)";
      let message = "" + this.prize.name;
      this.popupVisible = true;
      this.isClick = false;
    },
    getProductList: function () {
      getAllProduct().then(res => {
        if (res.data.resCode === 0) {
          //
          if (res.data.result.length > 0) {
            this.productList = res.data.result.slice(0, 2);
          }
        } else {
          Toast({
            message: res.data.resMsg,
            position: 'middle',
            duration: 2000
          });
        }
      });
    },
    gotoHotPorduct: function () {
      this.$router.push('/hot')
    },
    colsePopup: function () {
      this.popupVisible = false;
      this.prize = {};
    }

  },
  beforeDestroy() {
    clearInterval(this.timer);
    clearInterval(this.timerOuter);
    clearTimeout(this.timerOut);
    clearTimeout(this.timer2);
  }
}
</script>
